.cookie-banner {
  width: 100%;
  padding-top: 27px;
  padding-bottom: 22px;
  position: fixed;
  bottom: 0;
  background-color: white;
  box-shadow: -5px -20px 30px rgba(168, 176, 190, 0.5);
  z-index: 99;
  transform: translateY(100%);
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.cookie-banner.cookie-banner_open {
  transform: translateY(0);
  opacity: 1;
}
.cookie-banner .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cookie-banner_text {
  max-width: 1066px;
  margin-right: 40px;
  margin-top: 0;
  font-weight: normal;
  font-size: 18px;
  line-height: 40px;
  letter-spacing: -0.03em;
  color: var(--color-secondary);
}

@media (max-width: 1440px) {
  .cookie-banner_text {
    max-width: 938px;
    font-size: 16px;
  }
}
@media (max-width: 1024px) {
  .cookie-banner_text {
    max-width: 728px;
    font-size: 14px;
    line-height: 30px;
  }
}
@media (max-width: 768px) {
  .cookie-banner .container {
    flex-direction: column;
  }
  .cookie-banner_text {
    margin-right: 0;
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 18px;
  }
  .cookie-banner_button {
    height: 36px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
  }
}
